.header {
    background: #fff;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1002;
    height: 91px;
    width: 100%;
    box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.2);
    transition: all 0.25s ease;
    display: flex;
    padding: 11px 0 0;

    &.scroll-up,
    &.scroll-down {
        top: 0;
    }

    .container{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        flex-grow: 1;
        max-width: 100% !important;
    }

    &-burger{
        order: 1;
    }

    &-logo{
        order: 2;
        margin: 0 auto;
        max-width: 90px;
        img{
            width: 100%;
            height: auto;
        }
    }

    &-feedback{
        order: 3;
        display: flex;
        align-items: center;
        flex-shrink: 0;

        &__address{
            display: none;
        }

        &__instagram{
            display: flex;
            align-items: center;
        }

        &__phone{
            display: flex;
            align-items: center;
            margin-right: 11px;
            span{
                display: none;
            }
        }

        &__btn{
            display: none;
        }

    }

    &-nav{
        order: 4;
        width: calc(100% + 20px);
        border-top: 1px solid #D8D8D8;
        padding: 5px 0;
        margin: 8px -10px 0;
        &__ul{
            display: flex;
            li{
                flex-basis: 33.33%;
                flex-grow: 1;
                border-right: 1px solid #D8D8D8;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 0 10px;
                min-height: 26px;

                &:last-child{
                    border-right: none;
                }
                a{
                    font-size: 10px;
                    line-height: 130%;
                    text-align: center;
                    letter-spacing: 0.05em;
                    color: #262626;
                    text-align: center;
                    display: block;

                    &:hover{
                        font-weight: 700;
                        color: #8C52FF;
                    }

                }
            }
        }
    }

    @media screen and (min-width: 480px) {
        &-nav{
            width: calc(100% + 30px);
            margin: 8px -15px 0;
        }
    }

    @media screen and (min-width: 640px) {
        &-nav{
            width: calc(100% + 50px);
            margin: 8px -25px 0;

            &__ul li a{
                font-size: 14px;
            }
        }
    }

    @media screen and (min-width: 960px) {

        &-burger{
            margin-right: 35px;
        }

        &-logo{
            margin: 0 auto 0 0;
        }


        &-feedback{

            &__address{
                display: flex;
                align-items: center;
                margin-right: 41px;

                svg{
                    flex-shrink: 0;
                }

                span{
                    margin-left: 8px;
                    font-size: 14px;
                    line-height: 130%;
                    letter-spacing: 0.05em;
                    color: #262626;
                }

            }

            &__phone{
                margin-right: 40px;

                svg{
                    flex-shrink: 0;
                }

                span{
                    margin-left: 10px;
                    display: block;
                    font-size: 16px;
                    line-height: 130%;
                    font-weight: 700;
                    letter-spacing: 0.05em;
                }
            }
        }
    }

    @media screen and (min-width: 1280px) {
        height: 80px;
        padding: 18px 0;

        .container{
            flex-wrap: nowrap;
        }

        &-logo{
            flex-shrink: 0;
        }

        &-nav{
            order: 3;
            width: 100%;
            max-width: 500px;
            margin: 0 auto 0 0;
            padding: 0 20px;
            border: none;
            display: flex;
            align-self: stretch;
            &__ul{
                li{
                    padding: 0 20px;
                    flex-basis: auto;

                    a::after{
                        display: block;
                        content: attr(data-title);
                        font-weight: bold;
                        height: 0;
                        overflow: hidden;
                        visibility: hidden;
                    }
                }
            }
        }

        &-feedback{
            &__address{
                flex-shrink: 1;
                margin-right: 20px;
                max-width: 250px;
            }
            &__phone{
                flex-shrink: 0;
                margin-right: 20px;
            }
        }
    }

    @media screen and (min-width: 1440px){
        &-nav{
            flex-grow: 1;
            max-width: none;
            justify-content: center;
            padding: 0 20px;
        }
        &-feedback{
            &__address{
                order: 1;
            }

            &__instagram{
                margin-right: 36px;
                order: 2;
            }

            &__phone{
                order: 3;
                margin-right: 36px;
            }


        }

        &-nav{
            ul li a{
                font-size: 16px;
            }
        }

        &-feedback{
            &__address{
                margin-right: 42px;

                svg{
                    width: 24px;
                    height: 24px;
                }

                span{
                    font-size: 16px;
                    margin-left: 15px;
                }

            }

            &__phone{
                span{
                    font-size: 20px;
                }
            }

            &__instagram{
                svg{
                    width: 30px;
                    height: 30px;
                }

            }
        }

    }

    @media screen and (min-width: 1600px) {

        &-logo{
            max-width: 130px;
            width: 100%;
        }

        &-feedback{
            &__phone{
                margin-right: 56px;
            }
        }

        &-feedback{
            &__btn{
                display: flex;
                order: 4;
            }
        }

    }

}