.popup__wrp {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.35);
  display: flex;
  z-index: 1003;
  padding: 0px;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease;
  overflow-y: auto;
  overscroll-behavior-y: contain;

  @media screen and (min-width: 481px){
    padding: 20px;
  }

  &:target {
    opacity: 1;
    pointer-events: all;
  }

  .popup {
    max-width: 480px;
    position: relative;
    width: 100%;
    background: #fff;
    padding: 50px 20px 20px;
    align-items: flex-start;
    margin: auto 0 0 0;
    box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.3);


    @media screen and (min-width: 481px){
      margin: auto;
      padding: 40px;
      border-radius: 10px;
    }

    @media screen and (min-width: 640px) {
      &-close{
        right: -40px;
        svg path{
          fill: #fff;
        }
      }
    }

    @media screen and (min-width: 640px) and (min-height: 800px) {
      &-close{
        top: -36px;
      }
    }

    @media screen and (min-width: 960px) {
      max-width: 860px;
      min-height: 525px;
      position: relative;
      padding-top: 60px;
      padding-right: 428px;

      .section-h3{
        max-width: 436px;
        font-size: 30px;
        margin-bottom: 40px;
      }

      form{
        margin-right: auto;
        margin-left: 0;

        input:not([type=checkbox]):not([type=radio]), textarea{
          font-size: 20px;
          line-height: 150%;
        }

        .form__agreement{
          font-size: 14px;
          line-height: 150%;
        }

        .form__submit{
          .btn{
            padding: 15px 27px;
            font-size: 20px;
            line-height: 27px;
            height: 56px;
          }
        }

      }


      &-content__doctor{
        position: absolute;
        right: -27px;
        bottom: 0;
        max-width: 420px;

        .caption{
          font-size: 16px;
          line-height: 150%;
          padding-right: 47px;

          .blue{
            font-size: 20px;
            line-height: 150%;
          }

        }

        img{
          max-width: 455px;
          width: 100%;
          margin-left: auto;
          margin-right: 0;
        }


      }

    }


    @media screen and (min-width: 1152px) {
      max-width: 954px;
      min-height: 525px;

      &-content__doctor{
        max-width: 523px;
      }

    }



    &-close {
      position: absolute;
      top: 20px;
      right: 20px;
      width: 25px;
      height: 25px;

      svg path{
        fill: #cdcdcd;
      }

      a{
        display: block;
      }
    }

    &-content__doctor{
      width: 100%;
      max-width: 445px;
      margin-bottom: 30px;

      img{
        width: 100%;
        height: auto;
        max-width: 240px;
        margin-right: auto;
      }

      .caption{
        padding-top: 20px;
        text-align: right;
        font-size: 13px;
        line-height: 150%;

        .blue{
          font-size: 16px;
          line-height: 150%;
          margin-bottom: 5px;
          display: block;
        }

      }


    }

    .section-h3 {
      text-align: left;
      font-weight: 700;
      font-size: 22px;
      line-height: 150%;
      margin-bottom: 20px;
      letter-spacing: 0.05em;

      @media screen and (min-width: 640px) {
        text-align: center;
      }

    }

    form{
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 436px;
      margin: 0 auto;
    }

    .form__field{
      width: 100%;
    }

    .form__submit{
      width: 100%;
      max-width: 240px;
      margin: 0 auto;
      .btn{
        padding: 16px 25px;
      }
    }

    .form__agreement{
      margin-top: 20px;
      text-align: center;
      font-size: 10px;
      line-height: 130%;
      letter-spacing: 0.05em;
      color: #262626;

      a{
        text-decoration: underline;
      }

    }

    input:not([type=checkbox]):not([type=radio]), textarea{
      border: 1px solid #CCCCCC;
      border-radius: 10px;
    }



  }

}