$radius: 80px;

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 13px 19px;
  background: $btn;
  color: #fff;
  font-weight: 700;
  font-size: 13px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.05em;
  border: none;
  cursor: pointer;
  user-select: none;
  user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  transition: all 0.25s ease;

  @media screen and (min-width: 480px) {
    font-size: 15px;
  }

  &:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }

  &-blue {
    background: linear-gradient(90deg, #8D52FE 0%, #CA6CE7 100%);
    border-radius: 10px;
  }

  &-without-gradient{
    background: #8C52FF;
  }

  &-white--violet{
    color: #8c52ff;
    background: #FFFFFF;
    box-shadow: 0px 6px 15px rgba(140, 82, 255, 0.3);
    border-radius: 10px;
  }

  &-white {
    color: #CB6CE6;
    background: #FFFFFF;
    box-shadow: 0px 6px 15px rgba(140, 82, 255, 0.3);
    border-radius: 10px;
  }

}