.footer {
    margin-top: auto;
    padding: 30px 0 20px;
    background: #17171F;

    .container{
        display: flex;
        flex-wrap: wrap;
    }

    &-logo{
        margin-right: auto;
        width: 115px / 300px * 100%;
    }

    &-socials{
        margin-top: 17px;
        display: flex;
        li{
            margin-right: 5px;
        }
        li a{
            background: #22222B;
            border-radius: 20px;
            width: 35px;
            height: 35px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &-info{
        max-width: 170px;
        width: 100%;

        &__socials{
            display: none;
        }
        &__items{
            display: flex;
            flex-direction: column;
            align-items: flex-end;

            .item{
                display: flex;

                &.phone{
                    margin-bottom: 25px;
                }

                &-icon{
                    margin-right: 10px;
                }
                &-text{
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 150%;
                    text-align: right;
                    color: #CFCFD0;
                }

                &-text a{
                    font-weight: 700;
                    font-size: 16px;
                    line-height: 120%;
                    color: #F6F9FC;
                }
            }

        }
    }

    &-nav{
        width: 100%;
        margin-top: 40px;
        &__title{
            text-align: center;
            font-weight: 700;
            font-size: 13px;
            line-height: 120%;
            color: #8B8B8F;
            padding-bottom: 11px;
            border-bottom: 1px solid #8B8B8F;
        }
        &__ul{
            padding-top: 20px;
            padding-bottom: 40px;
            columns: 2;
            column-gap: 20px;

            li{
                break-inside: avoid;
            }

            ul.section-ul{
                & > li{
                    padding: 6px 0;
                }
                & > li > a{
                    font-weight: 700;
                }
                li a{
                    font-size: 10px;
                    line-height: 120%;
                    color: #F6F9FC;
                    display: block;
                    padding: 4px 0;
                }
            }

        }
    }

    &-about{
        border-bottom: 1px solid #8B8B8F;
        padding-bottom: 14px;
    }

    &-policy{
        padding-top: 20px;
    }

    &-about,
    &-policy{
        width: 100%;
        text-align: center;

        a, p{
            font-weight: 400;
            font-size: 10px;
            line-height: 120%;
            color: #8B8B8F;
            margin: 6px 0;
        }

        a{
            display: block;
        }

    }

    @media screen and (min-width: 480px) {

        &-nav__ul{
            columns: 3;
        }
    }

    @media screen and (min-width: 640px) {

        &-logo{
            max-width: 130px;

            &__img{
                img{
                    width: 100%;
                    height: auto;
                }
            }

        }

        &-info{
            max-width: 310px;
        }



        &-about,
        &-policy{
            padding: 0;
            border: none;
        }

        &-about{
            width: 260px / 620px * 100%;
            margin-right: auto;
            text-align: left;
        }

        &-policy{
            width: 180px / 620px * 100%;
            text-align: right;
            align-self: flex-end;
        }

    }

    @media screen and (min-width: 830px){
        &-nav__ul{
            columns: 4;
        }
    }

    @media screen and (min-width: 1024px){
        &-nav__ul{
            columns: 5;
        }
    }

    @media screen and (min-width: 1152px){
        &-nav__ul{
            columns: 6;
        }
    }

    @media screen and (min-width: 1280px){

        &-nav__title{
            font-size: 16px;
        }

        &-logo__socials{
            display: none;
        }

        &-info{
            width: 100%;
            max-width: calc(100% - 150px);
            display: flex;
            align-self: center;
            justify-content: flex-end;
        }

        &-info__socials{
            display: flex;
            margin: 0 30px 0 0;
        }

        &-socials li{
            margin-right: 20px;
        }

        &-socials li a{
            width: 40px;
            height: 40px;
        }

        &-info__items .item.phone{
            margin-bottom: 10px;
        }

        &-nav__ul ul.section-ul > li > a{
            font-size: 13px !important;
        }

        &-nav__ul ul.section-ul li a{
            font-size: 12px;
            padding: 7px 0;
        }

        &-about{
            width: 600px / 1200px * 100%;
        }

        &-policy{
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-end;
            width: 420px / 1200px * 100%;

            a{
                display: block;
                &:first-child::after{
                    display: inline-block;
                    content: "I";
                    padding: 0 10px;
                }
            }

        }

        &-about,
        &-policy{

            a, p{
                font-size: 12px;
            }

        }


    }

}

.scroll-top{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 56px;
    height: 56px;
    border-radius: 50%;
    position: fixed;
    bottom: 24px;
    right: 24px;
    transform: translateY(56px + 24px);
    transition: all 0.25s ease;
    background: #fff;
    cursor: pointer;
    z-index: 2;

    &.is-active{
        transform: translateY(0);
        box-shadow: 0 0 9px rgba(0,0,0, 0.25);
    }
}