$mBack: #eee;
$mWidth: 320px;
.mobileMenu {
    display: block;
    position: fixed;
    top: 91px;
    left: 0;
    bottom: 0;
    transform: translateX(-100%);
    width: $mWidth;
    transition: transform 0.3s ease;
    overflow-y: auto;
    z-index: 1001;
    background: #FFFFFF;

    @media screen and (min-width: 1280px) {
        top: 80px;
    }

    &.opened {
        transform: translateX(0);
        box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
    }
    &-header {
        &__toggle {
            display: flex;
            align-items: center;
            height: 50px;
        }
    }
    &-nav {

        &__ul > li{
            border-bottom: 1px solid #D8D8D8;
        }

        &__ul li {

            & > a{
                font-weight: bold;
            }

            a{
                font-size: 13px;
                line-height: 130%;
                letter-spacing: 0.05em;
                color: #262626;
                display: block;
                padding: 20px 0;
            }

            &.has-submenu{
                display: flex;
                flex-wrap: wrap;
                align-items: center;

                .has-submenu__toggle{
                    cursor: pointer;
                    transition: all 0.25s ease;
                    display: block;
                    width: 28px;
                    height: 56px;
                    background-repeat: no-repeat;
                    background-position: center;
                    background-image: url("data:image/svg+xml,%3Csvg width='16' height='9' viewBox='0 0 16 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Crect width='11' height='1.69231' rx='0.846154' transform='matrix(0.70625 0.707962 -0.70625 0.707962 1.19531 0)' fill='%238C52FF'/%3E%3Crect width='11' height='1.69231' rx='0.846154' transform='matrix(0.70625 -0.707962 0.70625 0.707962 7 7.7876)' fill='%238C52FF'/%3E%3C/svg%3E%0A");
                }

                & > a{
                    width: calc(100% - 28px);
                }

                &.is-active{
                    padding-bottom: 14px;

                    .has-submenu__toggle{
                        transform: scaleY(-1);
                    }

                    .submenu{
                        display: block;
                    }
                }

                .submenu{
                    display: none;
                    width: 100%;

                    a{
                        font-weight: normal;
                        display: block;
                        padding: 0 0 6px 0;
                    }

                }
            }

        }
    }
}