.section-h1,
h1 {
    font-size: 30px;
    line-height: 130%;

    .violet{
        display: block;
    }

    @media screen and (min-width: 480px) {
        font-size: 40px;
    }

    @media screen and (min-width: 640px) {
        font-size: 45px;
    }

    @media screen and (min-width: 830px) {
    }

    @media screen and (min-width: 1152px) {
        font-size: 48px;

    }

}

.page-detail{
    .section-h2,
    h2 {
        font-size: 26px;
        line-height: 130%;

        @media screen and (min-width: 640px) {
            font-size: 32px;
        }

        @media screen and (min-width: 960px) {
            font-size: 44px;
        }

        @media screen and (min-width: 1152px) {
            font-size: 50px;
        }
    }
}

.section-h2,
h2 {
    font-size: 26px;
    line-height: 130%;

    @media screen and (min-width: 480px) {
        font-size: 34px;
    }
    @media screen and (min-width: 640px) {
        font-size: 45px;
    }
    @media screen and (min-width: 1152px) {
        font-size: 56px;
    }
}

.section-h3,
h3 {
    font-size: 20px;
    line-height: 150%;

    @media screen and (min-width: 640px) {
        font-size: 30px;
    }

    @media screen and (min-width: 1152px) {
        font-size: 40px;
    }

}

.bold{
    font-weight: 700;
}

.violet{
    color: #3B2072;
}

.blue{
    color: #8C52FF;
}

.cyan{
    color: #5271FF;
}

.pink{
    color: #CB6CE6;
}