.tabs__block {

  .tabs__toggles {
    display: flex;
    flex-wrap: wrap;
  }

  .tabs__toggle {
    background: #FFFFFF;
    border-radius: 10px;
    padding: 15px 9px;
    transition: all 0.25s ease;
    text-align: center;
    width: 50%;
    font-size: 13px;
    line-height: 120%;
    letter-spacing: 0.05em;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 5px 0;
    cursor: pointer;
  }

  .tabs__toggle.is-active {
    background: #8C52FF;
    color: #fff;
    font-weight: 700;
  }

  .tabs__content {
    display: none;
  }

  .tabs__content.is-active {
    display: block;
  }
}

.swiper-button-next, .swiper-button-prev{
  color: #8C52FF !important;
}

.swiper-pagination-bullet{
  background: #B3B3B3 !important;
  color: #B3B3B3 !important;
}

.swiper-pagination-bullet-active{
  background: #8C52FF !important;
  color: #8C52FF !important;
}