$symbol: '→';

.breadcrumb {
    background-color: transparent;
    padding: 20px 0;

    @media screen and (min-width: 640px) {
        padding: 29px 0;
    }

    @media screen and (min-width: 1152px) {
        padding: 40px 0 29px;
    }

    & ul {
        display: flex;
        align-items: center;
        list-style-type: none;
        overflow-x: auto;
        white-space: nowrap;
    }
    & li {
        & a {
            font-size: 12px;
            line-height: 130%;
            letter-spacing: 0.05em;
            color: #8B8B8F;
            &:hover {
                text-decoration: underline;
            }

            @media screen and (min-width: 640px) {
                font-size: 14px;
                line-height: 130%;
            }

        }
        &::after {
            content: $symbol;
            display: inline-block;
            border-radius: 50%;
            position: relative;
            padding: 0 5px;
            color: #8B8B8F;
        }
        &:last-child::after {
            display: none;
        }
    }
}